.custom-container{
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.title{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
}

.title h2{
    margin-left: 5rem;
}

.title img{
    height: 5rem;
}

.chat-container{
    border-radius: 2rem;
    border: black 1px solid;
    text-align: right;
    padding-top: 3rem;
    height: 50rem;
    width: 50%;
    margin: auto;
    margin-top: 1rem;
}

.chat{
    height: 90%; 
    background-color: rgb(199, 199, 199);
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
    padding-top: 3rem;
    margin-top: -3rem;
}

.chat-container hr{
    margin-top: -0px;
}

.input-field{
    height: 4rem;
    margin: auto;
    padding-top: 1rem;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    background-color: rgb(199, 199, 199);
}

.input-field form{
    padding-top: .7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    border-top: 1px black solid;
}

.input-text{
    width: 75%;
    height: 2rem;
    padding-left: 1rem;
    border-radius: 1rem;
    border: 1px white solid;
    background-color: transparent;
    color: black;
    margin-left: 1rem;
}

.custom-button{
    border-radius: 1rem;
    background-color: rgb(67, 70, 255); 
    color: black;
    height: 2rem;
    width: 5rem;
    border: rgb(34, 37, 255) 1px solid;
    margin-left: 2rem;
    margin-right: 1rem;
}

.custom-button:hover{
    background-color: rgb(34, 37, 255);
}

.custom-button:active{
    background-color: rgb(68, 71, 255);
}

.message{
    display: flex;
    flex-direction: column;
}

.legal{
    font-size: 12px;
    font-weight: 100;
}